import React, { Fragment } from 'react';
import { Anchor, Ellipse, Cylinder, Box, Hemisphere, Rect, RoundedRect, Group, Shape, Polygon } from 'react-zdog';

import { FShape, AmpShape } from '../shapes/letters';

const DARK_AQUA = "#84DCC6";
const LIGHT_AQUA = "#A5FFD6";
const LIGHT_RED = "#FFA69E";
const DARK_RED = "#FF686B";
const YELLOW = "#FFCE49";

const ShapeExtrusion = ({ fill=true, color, sideColor, depth=10, spacing=1.3, ...props }) => (
    <Anchor>
        <Group>
            {[...Array(depth)].map((n, i) => (
                <AmpShape
                    color={ i === 0? color : (sideColor? sideColor : color) }
                    translate={{ z: i * spacing }}
                    fill={ fill && i === 0 }
                    {...props} />
            ))}
        </Group>
    </Anchor>
);

export default ShapeExtrusion;